import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  root: {
    display: 'flex',
  },
  formControl: {
    margin: theme.spacing.unit * 3,
  },
  group: {
    margin: `${theme.spacing.unit}px 0`,
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200,
  },
});

class Database extends React.Component{
  
  state = {
    useExternalDatabase: false,
    type: 'oracle'
  };

  handleChange = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  handleRadioChange = event => {
    this.setState({ type: event.target.value });
  };



  render() {
    const { classes } = this.props;
    return (
      <div className="step1">
        <Typography variant="title" gutterBottom>
          CMS/Configuration Database
        </Typography>
        <Grid item xs={12}>
            <FormControlLabel
              control={<Checkbox color="secondary" name="external_database" value="yes" onChange={this.handleChange('useExternalDatabase')}/>}
              label="External Database"
            />
          </Grid>
        { this.state.useExternalDatabase && 
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>
            <FormControlLabel
              checked={this.state.type === 'oracle'}
              value="oracle"
              control={<Radio color="primary" />}
              label="Oracle"
              labelPlacement="end"
              onChange={this.handleRadioChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <FormControlLabel
              checked={this.state.type === 'postgresql'}
              value="postgresql"
              control={<Radio color="primary" />}
              label="PostgreSQL"
              labelPlacement="end"
              onChange={this.handleRadioChange}
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <TextField
              required
              id="city"
              name="city"
              label="City"
              fullWidth
              autoComplete="billing address-level2"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField id="state" name="state" label="State/Province/Region" fullWidth />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              id="zip"
              name="zip"
              label="Zip / Postal code"
              fullWidth
              autoComplete="billing postal-code"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              id="country"
              name="country"
              label="Country"
              fullWidth
              autoComplete="billing country"
            />
          </Grid>
          
        </Grid>
        }
          
      </div>
    );
  }
  
}

export default withStyles(styles)(Database);


